




import {Component, Prop, Vue} from 'vue-property-decorator';

@Component({})
export default class ViewComponent extends Vue {
    kaptcha: string = ''
    imgCode: string = ''

    setImgCode() {
      this.imgCode = 'Wxxxxxxxx-xxxx-xxxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, (c) => {
        const r = Math.random() * 16 | 0;
        const v = (c === 'x') ? r : (r & 0x3 | 0x8);
        return v.toString(16);
      });
    }

    async initVerifyImg() {
      this.setImgCode();
      const data = await this.$request({
        direct: 'api/2.0/m/kaptcha',
        // url: '//10.10.0.36:8080/api/2.0/m/kaptcha',
        params: {
          uniqueSign: this.imgCode
        }
      })
      if (data && data.success) {
        this.kaptcha = data.imageUrl;
        this.$emit('update:uuid', this.imgCode);
      }
    }

    created() {
      this.initVerifyImg();
    }
}

