








import { Component, Vue, Prop } from 'vue-property-decorator';
@Component({
  components: {},
})
export default class ViewComponent extends Vue {
  /**prop */
  @Prop() prop!: any;

  /**computed */
  get action() {
    return {
      // target: (this.$store.state.domainApi ? `//${this.$store.state.domainApi}` : this.$store.state.originNodeServer)+'/api/3.0/M/' + this.prop.api,
      target: this.$store.state.domainNodeServer+'/api/3.0/m/' + this.prop.api,
      data: {
        params: JSON.stringify({
          ...this.prop.params,
        }),
        from: 'M',
        imei: 'imei',
        appKey: '99999999',
      },
      checkSuccess:(res:any,file:any)=>{
        // console.log('checkSuccess	res:',res)
        // console.log('checkSuccess	file:',file)
        if(res.resCode === '0000'){

          return true
        }else{
          return false
        }
        
      }
    }
  }

  /**life hook */
  created(){
    // console.log('target:',(this.$store.state.domainApi ? `${location.protocol}//${this.$store.state.domainApi}` : this.$store.state.originNodeServer)+'/api/3.0/M/' + this.prop.api)
  }
}
