  }
  async send() {
    countdownSecond({
      self: this,
      dataSelector: 'countdownText',
      textConcat(second: number) {
        return  `${second}秒后可重新发送`
      },
      timeout: 60,
      callback : () => {
        this.countdownText = '重新发送'
      },
    })

    const params = {
      mobile: this.vm.model.mobile,
      verifyType: this.vm.model.verifyType,
      uniqueSign: this.vm.model.uuid,
      imageVerifyCode: this.vm.model.imageVerifyCode  
    }
    const data = await this.$request({
      direct: '/api/2.0/m/sendVerifyCodeForImageVerifyCode',
      params
    })
    
    if (data && (data.success === true)) {
      
    }
  }
